.card {
    background-image: -webkit-linear-gradient(135deg, #FF4D38, #F42F54);
    background-image: -o-linear-gradient(135deg, #FF4D38, #F42F54);
    background-image: linear-gradient(-45deg, #FF4D38, #F42F54);
    width: 300px;
    height: 185px;
    border-radius: 16px;
    margin-top: 40px;
}

.card-logo {
    width: 90px;
    margin-left: 16px;
    margin-top: 16px;
}

.name {
    margin-left: 16px;
    margin-top: 16px;
    margin-bottom: 0px;
    color: #111E6B;
    font-family: 'Helvetica';
    font-weight: bold;
    font-size: 1.3em;
}

.balance {
    color: white;
    font-family: 'Helvetica';
    font-weight: bold;
    font-size: 2em;
    text-align: right;
    margin-right: 16px;
    margin-top: 40px;
}


