.container {
    padding-top: 10%;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-line-pack: center;
        align-content: center;
    margin-left: auto;
    margin-right: auto;
}

.forma {
    height: auto;
    text-align: center;
}

.buttons {
    margin-left: auto;
    margin-right: auto;
    display: inline-block;
    width: 280px;
}

.header {
    display: inline;
}

.titleName {
    display: inline;
    color: #00CCFF;
    font-family: 'Helvetica';
    font-weight: bold;
    position: relative;
    top: 5px;
    left: 25px;
}

.backLiink {
    display: inline;
    background-color: #00CCFF;
    height: 40px;
    width: 40px;
    display: table-cell;
    text-align: center;
    vertical-align: middle;
    border-radius: 50%;
    font-family: 'Helvetica';
    font-weight: bold;
    font-size: 2em;
    color: white;
    text-decoration: none;
    position: relative;
    top: 15px;
    left: 15px;
}


