body {
    background-color: white;
}

.balance-view {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

.leftContainer {
    margin-top: 15%;
    left: 0px;
    width: 50%;
}

.rightContainer {
    margin-top: 15%;
    margin-left: auto;
    margin-right: auto;
    right: 0px;
    width: 50%;
}

.form {
    height: auto;
    position: relative;
    top: 50%;
    left: 50%;
    text-align: center;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.buttons {
    margin-left: auto;
    margin-right: auto;
    display: inline-block;
    width: 350px;
}


