@import "../../assets//css//bootstrap-grid.css";

.transactionCardadd {
  border-color: #3DD87F;
  margin-left: 10%;
  border-style: solid;
  border-width: 5px;
  border-radius: 50px;
  max-width: 250px;
  max-height: 61px;
}

.transactionCardsub {
  border-color: #F42F54;
  margin-left: 10%;
  border-style: solid;
  border-width: 5px;
  border-radius: 50px;
  max-width: 250px;
  max-height: 61px;
}

.joiner {
  padding: 0;
  margin: 0;
  background-color: #111E6B;
  height: 40px;
  width: 5px;
  margin-left: 17%;
  position: relative;
}

.transaction-icon{
  margin-top: 10px;
  width: 40px;
  height: 40px;
}

.circle{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
}

.transac_cantadd {
  color: #3DD87F;
  font-family: 'Helvetica';
  font-weight: bold;
  font-size: 20px;
  position: relative;
  top: 4px;
  padding: 0;
  margin: 0;
}

.transac_cantsub {
  color: #F42F54;
  font-family: 'Helvetica';
  font-weight: bold;
  font-size: 20px;
  position: relative;
  top: 4px;
  padding: 0;
  margin: 0;
}

.circleAdd {
  background-color: #3DD87F;
  height: 60px;
  width: 60px;
  display: table-cell;
  text-align: center;
  vertical-align: middle;
  border-radius: 50%;
  font-family: 'Helvetica';
  font-weight: bold;
  font-size: 3em;
  color: white;
  position: relative;
  left: -4px;
  top: -4px
}

.circleSub {
  background-color: #F42F54;
  height: 60px;
  width: 60px;
  display: table-cell;
  text-align: center;
  vertical-align: middle;
  border-radius: 50%;
  font-family: 'Helvetica';
  font-weight: bold;
  font-size: 3em;
  color: white;
  position: relative;
  left: -4px;
  top: -4px
}

.transac_desc {
  color: #111E6B;
  font-family: 'Helvetica';
  font-weight: bold;
  font-size: 1.1em;
  position: relative;
  top: 4px;
  padding: 0;
  margin: 0;
}

.balanceInfo {
  position: relative;
  left: -20px;
}


